const Noticia = (props) => {
    const { urlToImage, url, title, description, source } = props.noticia;
    let imagen;
    if (urlToImage !== '') {
        imagen = <img src={urlToImage} alt={title} className="card-img-top"></img>
    } else {
        imagen = <img src={'https://i2.wp.com/criptotendencia.com/wp-content/uploads/2019/03/Bitcoin-supera-los-4000-dolares-marzo-2019.jpg?fit=1920%2C1200&ssl=1'} alt={title} className="card-img-top"></img>
    }

    return (
        <div className="col-md-6 col-12 mb-4">
            <div className="card">
                <div className="card-image">
                    {imagen}
                </div>
            </div>
        </div>
    )
}

export default Noticia
